#textblock {
  background-color: #444348;
  height: 200vh;
  margin-top: 34%;
  overflow: hidden;
  padding-bottom: 800px;
}

#textblock-container {
  position: relative;
  width: 50%;
  height: auto;
  margin: 0 auto;
  margin-top: 80px;
  padding: 30px;
  padding-top: 0px;
  border-radius: 20px;
  background-color: #444348;
  box-shadow: 0 5px 0px 6px #252427;
} 

#textblock-title {
  color: #dcdcdc;
  font-size: 35px;
  font-weight: 600;
  font-family: "Helvetica Neue";
  display: inline-block;
}

#super-title {
  color: #e7cc50;
  font-size: 35px;
  font-weight: 600;
  font-family: "Helvetica Neue";
