  font-size: 75px;
  font-weight: 600;
  font-family: "Helvetica Neue";
}

#textblock-content {
  line-height: 35px;
  color: #dcdcdc;
  font-size: 20px;
}

#textblock-footer {
  color: #000000;
  font-size: 15px;
  font-weight: 400;
  position: fixed;
  width: 100%;
  bottom: 0px;
  justify-content:center;
  align-items:center;
  text-align:center;
  margin-bottom: 20px;

}

#textblock-devsense {
  text-decoration: none;
  color: black;
  font-size: 15px;
  font-weight: 600;
}

#icons {
  position: absolute;
  display: inline-block;
  margin-left: auto;
  right: 40px;
  top: 20px;
}

#icon:hover {
  color: #dcdcdc;
}

#icon {
  margin-left: 10px;
  color: #9b9b9b;
}

#txt-btn:hover {
  color: #dcdcdc;
}

#txt-btn {
  display: inline-block;
  line-height: 35px;
  color: #9b9b9b;
  font-size: 20px;
  border-style: solid;
  border-width: 0.01cm;
  border-color: #dcdcdc;
  border-radius: 5px;
  padding: 5px;
}

#project-container {
  display: inline-block;
  margin-top: 80px;
  margin-left: 10px;
  border-radius: 20px;
  width: 400px;
  height: auto;
}

#project-container-title {
  margin: 15px;
  color: #dcdcdc;
  font-size: 35px;
  font-weight: 600;
  font-family: "Helvetica Neue";
  display: inline-block;
}

#project-container-content {
  margin-left: 15px;
  margin-right: 15px;
  line-height: 35px;
  color: #dcdcdc;
  font-size: 16px;
}

#redash {
  display: inline-block;
  width: 400px;
  height: auto;
  border-radius: 20px;
}

.grid-container {
  margin: auto;
  width: 60%;
  display: grid;
  column-gap: 50px;
  grid-template-columns: auto auto;
  justify-content: space-evenly;
}

.grid-item {
  display: inline-block;
  position: relative;
  box-shadow: 0 3px 0px 4px #252427;
}

.grid-container2 {
  margin: auto;
  width: 100%;
  height: auto;
  display: grid;
  column-gap: 30px;
  grid-template-columns: auto auto auto auto;
  justify-content: space-evenly;
}

.grid-item2 {
  display: inline-block;
  align-content: space-evenly;
}

#project_icon {
  margin-left: 10px;
  color: #000000;
}

#skill-title {
  color: #dcdcdc;
  font-size: 25px;
  font-weight: 600;
  font-family: "Helvetica Neue";
  display: block;
  margin-left: 5px;
}

#skill-set {
  display: inline-block;
  width: 200px;
  align-content: space-evenly;
}

#skill {
  border-style: solid;
  border-width: 0.01cm;
  border-color: #dcdcdc;
  border-radius: 5px;
  margin: 6px;
  padding: 5px;
  display: inline-block;
  line-height: 35px;
  color: #dcdcdc;
  font-size: 20px;
  white-space: nowrap;
}

.animation, .animation_layer {
	height: 1000px;
  overflow: hidden;
}

.animation, .animation_small_layer {
	height: 1000px;
  overflow: hidden;
}

.animation {
  display: inline-block;
	position: relative;
	z-index: 10;
  height: min-content;
  overflow: hidden;
  margin: 0;
}

.animation_layer {
  background-position: 50% 0%;
  margin-top: 10%;
  background-repeat: no-repeat;
	width: 100%;
  height: 150%;
	position: absolute;
}
.animation_layer.parallax {
	position: fixed;
}

.animation_small_layer {
	background-position: 70% 50%;
	background-size: auto 200px;
	background-repeat: no-repeat;
	width: 100%;
	position: absolute;
  overflow: hidden;
}
.animation_small_layer.parallax {
	position: fixed;
  overflow: hidden;
}

