.card {
  /* Sizing */
  width: 100%;
  height: auto;
  margin: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: relative;
  transform-style: preserve-3d;
}

.card-front,
.card-back {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  padding: 30px;
  padding-top: 0px;
  border-radius: 20px;
  background-color: #444348;
  box-shadow: 0 5px 0px 6px #252427;
}

.card-back {
  
  transform: rotateY(180deg);
}

#positionkeeper {
  position: relative;
  visibility: hidden;
  padding: 10px;
}

#invis {
  visibility: hidden;
}

#hovering {
  position: absolute;
  top: 0;
}

#contact-content:hover {
  color: #dcdcdc;
}

#contact-content {
  display: inline-block;
  line-height: 35px;
  margin-left: 20px;
  margin-top: 5px;
  color: #9b9b9b;
  font-size: 26px;
}

#instructions {
  position: absolute;
  bottom: 40px;
  right: 20px;
}

#instruction-content {
  display: inline;
  line-height: 35px;
  margin-left: 20px;
  margin-top: 5px;
  color: #dcdcdc;
  font-size: 26px;
}

#turnover {
  margin-left: 20px;
  margin-right: 20px;
  display: inline;
  width: 80px;
  color: #dcdcdc;
}